import React from "react";
import { Container, Row, Col } from "react-bootstrap";
import { StaticImage } from "gatsby-plugin-image";

const CentralizedWork = () => {
  return (
    <>
      <Container>
        <Row className="mt_50 pt_50 mb_50 rg_50">
          <Col md={12}>
            <h2 className="text-center">
              How Does a Centralized Exchange Work?
            </h2>
          </Col>
          <Col lg={4} md={6} className="col_flex">
            <div className="centralize_work_card">
              <StaticImage
                className="centralize_card_icon"
                src="../../styles/assets/images/centralize-exchange/register.webp"
                formats={["auto", "webp"]}
              />

              <span className="centralize_title">User Registration</span>
              <p>
                Users create accounts on the centralized exchange, providing
                necessary details and completing the registration process.
              </p>
            </div>
          </Col>
          <Col lg={4} md={6} className="col_flex">
            <div className="centralize_work_card">
              <StaticImage
                className="centralize_card_icon"
                src="../../styles/assets/images/centralize-exchange/deposit.webp"
                formats={["auto", "webp"]}
              />

              <span className="centralize_title">Deposit Funds</span>
              <p>
                Once registered, users deposit cryptocurrencies or fiat currency
                into their exchange wallets.
              </p>
            </div>
          </Col>
          <Col lg={4} md={6} className="col_flex">
            <div className="centralize_work_card">
              <StaticImage
                className="centralize_card_icon"
                src="../../styles/assets/images/centralize-exchange/placement.webp"
                formats={["auto", "webp"]}
              />

              <span className="centralize_title">
                Order Placement and Matching
              </span>
              <p>
                Users place buy or sell orders, specifying prices and
                quantities. The exchange's matching engine pairs compatible
                orders, executing trades.
              </p>
            </div>
          </Col>
          <Col lg={{ span: 4, offset: 2 }} md={6} className="col_flex">
            <div className="centralize_work_card">
              <StaticImage
                className="centralize_card_icon"
                src="../../styles/assets/images/centralize-exchange/execute.webp"
                formats={["auto", "webp"]}
              />

              <span className="centralize_title">
                Execution and Confirmation
              </span>
              <p>
                When orders match, the exchange executes the trade, transferring
                assets between buyer and seller accounts. Both parties receive
                confirmations.
              </p>
            </div>
          </Col>
          <Col lg={4} md={12} className="col_flex">
            <div className="centralize_work_card">
              <StaticImage
                className="centralize_card_icon"
                src="../../styles/assets/images/centralize-exchange/withdraw.webp"
                formats={["auto", "webp"]}
              />

              <span className="centralize_title">Withdrawal of Funds</span>
              <p>
                Users can withdraw their funds, converting them back to
                cryptocurrencies or fiat, and transfer them to personal wallets
                or bank accounts.
              </p>
            </div>
          </Col>
        </Row>
      </Container>
    </>
  );
};

export default CentralizedWork;
