import React, { Fragment } from "react";
import { Col, Container, Row } from "react-bootstrap";
import TalkToUsNew from "../../components/common/talk-to-us-new";
import ConnectButton from "../../components/common/connect-button";
import { Link } from "gatsby";
import CtaButton from "../../components/common/contactbutton";
import Faq from "../../components/common/faq";
import CentralizedWork from "./centralized-work";
import CentralizedProcess from "./centralized-process";
import { StaticImage } from "gatsby-plugin-image";

const CentralizedCryptoScreen = (props) => {
  const { db } = props;

  const CentralizedFaq = [
    {
      question: "What is a centralized crypto exchange?",
      answer:
        "Centralized crypto exchange: Platform for trading, managed by central authority.",
    },
    {
      question: "What are popular centralized Exchanges?",
      answer: "Binance, Coinbase, Kraken, Kucoin, Bitfinex and so on.",
    },
    {
      question: "Estimated cost to build a centralized crypto exchange?",
      answer: "It depends on features and customization.",
    },
    {
      question: "Why Centralized crypto exchange?",
      answer:
        "Efficient trading, liquidity, user-friendly experience – benefits of centralized crypto exchanges.",
    },
  ];
  return (
    <Fragment>
      <div className="centralized_bg">
        <Container>
          <Row className="rg_50">
            <Col xl={7} lg={12}>
              <div className="d-flex flex-column rg_50 justify-content-center">
                <h1>Centralized Exchange Development Company</h1>
                <p className="text-white">
                  Get our centralized exchange development firm to help you
                  build a feature-rich cryptocurrency exchange platform. Deliver
                  your users with a top-notch solution to make cryptocurrency
                  trading as simple as possible!
                </p>
                <div className="decenter_hero_sec_btn">
                  <Link to="/contact-us" className="demo_btn">
                    View Live Demo
                  </Link>
                  <button type="submit" className="consult_btn">
                    <TalkToUsNew label="Free Consult" db={db} />
                  </button>
                </div>
              </div>
            </Col>
            <Col xl={5} lg={12} className="col_flex">
              <StaticImage
                src="../../styles/assets/images/centralize-exchange/cexchange.webp"
                formats={["auto", "webp"]}
              />
            </Col>
          </Row>
        </Container>
      </div>
      <div className="centralize_card_wrapper">
        <div className="centralize_card_blob"></div>
        <div className="centralize_card_blob_two"></div>
        <Container>
          <Row className="mt_50 pt_50">
            <div className="centralize_text_card">
              <Col xl={8} className="mx-auto text-center">
                <h2>Centralized Cryptocurrency Exchange Development Company</h2>
              </Col>
              <Col xl={10} className="mx-auto">
                <p>
                  Cryptocurrencies are in full swing as more people use these
                  computer-based digital currencies. Centralized crypto
                  exchanges are key to helping millions of users enter this new
                  money world. Our company helps create these platforms, making
                  it easy for users to switch to Web3 with interfaces similar to
                  what they already know from Web2.
                </p>
              </Col>
              <Col xl={10} className="mx-auto">
                <p>
                  Our experts can bring your crypto exchange ideas to life using
                  advanced technology. Choose between our complete exchange
                  development services or ready-made white-label exchange
                  software based on your business needs. Contact our experts now
                  to know more.
                </p>
              </Col>
              <Col md={12}>
                <ConnectButton justifyContentPosition="connect_center" />
              </Col>
            </div>
          </Row>
        </Container>
      </div>
      <CentralizedWork />
      <CtaButton
        db={db}
        dynamicHeaderText="Drive Your Business to Success with <br/> Our Tailored Services!"
      />
      <Container>
        <Row className="mt_50  rg_30 mb_50 pb_50">
          <Col xl={8} className="mx-auto text-center mt_50">
            <h2>
              Popular Crypto Trading Types in Centralized Exchanges (CEXs)
            </h2>
          </Col>
          <Col xl={10} className="mx-auto text-center">
            <p>
              To further enhance trader functionality, we help you launch a
              centralized cryptocurrency exchange application that includes
              multiple trading types. We can design platforms tailored to your
              target market with all the necessary features to guarantee simple
              user experiences.
            </p>
          </Col>
          <Col md={6}>
            <div className="centralize_trade_type_box">
              <span className="trade_type_title">Spot Trading</span>
              <p>
                Spot Trading involves the immediate exchange of financial
                instruments, such as currencies or commodities, at the current
                market price. It is characterized by instant settlement and is
                commonly used for short-term transactions.
              </p>
              <svg
                className="trade_type_svg"
                width="37"
                height="154"
                viewBox="0 0 37 154"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
              >
                <rect
                  opacity="0.1"
                  x="17.5237"
                  y="78.0005"
                  width="24.7822"
                  height="24.7822"
                  rx="4"
                  transform="rotate(45 17.5237 78.0005)"
                  fill="#7534fc"
                ></rect>
                <rect
                  opacity="0.3"
                  x="47.585"
                  y="92.9392"
                  width="41.1567"
                  height="45.1468"
                  rx="5"
                  transform="rotate(45 47.585 92.9392)"
                  fill="#7534fc"
                ></rect>
                <rect
                  opacity="0.8"
                  x="57.7432"
                  y="-2.99951"
                  width="78.8328"
                  height="78.8328"
                  rx="10"
                  transform="rotate(45 57.7432 -2.99951)"
                  fill="#7534fc"
                ></rect>
              </svg>
            </div>
          </Col>
          <Col md={6}>
            <div className="centralize_trade_type_box">
              <span className="trade_type_title">Future Trading</span>
              <p>
                Future Trading, on the other hand, involves agreements to buy or
                sell assets at a predetermined price on a specified future date.
                This type of trading allows investors to hedge against price
                fluctuations and can be used for speculation on the future
                direction of prices.
              </p>
              <svg
                className="trade_type_svg"
                width="37"
                height="154"
                viewBox="0 0 37 154"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
              >
                <rect
                  opacity="0.1"
                  x="17.5237"
                  y="78.0005"
                  width="24.7822"
                  height="24.7822"
                  rx="4"
                  transform="rotate(45 17.5237 78.0005)"
                  fill="#7534fc"
                ></rect>
                <rect
                  opacity="0.3"
                  x="47.585"
                  y="92.9392"
                  width="41.1567"
                  height="45.1468"
                  rx="5"
                  transform="rotate(45 47.585 92.9392)"
                  fill="#7534fc"
                ></rect>
                <rect
                  opacity="0.8"
                  x="57.7432"
                  y="-2.99951"
                  width="78.8328"
                  height="78.8328"
                  rx="10"
                  transform="rotate(45 57.7432 -2.99951)"
                  fill="#7534fc"
                ></rect>
              </svg>
            </div>
          </Col>
          <Col md={12} className="mt_30">
            <div className="crypto_btns_grp justify-content-center ">
              <Link className="prime_submit_btn" to="/contact-us">
                View Live Demo
              </Link>
              <button className="second_consult_btn" type="button">
                <TalkToUsNew label="Free Consult" db={db} />
              </button>
            </div>
          </Col>
        </Row>
        <Row className="rg_30 mt_50 pt_50 mb_50">
          <Col xl={12} className="mx-auto text-center">
            <h2>Key Features Of Centralized Exchange Platform</h2>
          </Col>
          <Col xl={8} className="mx-auto text-center">
            <p>
              As a leading centralized crypto exchange development company, we
              assure complete coverage and distinctive business-oriented
              features for our CEX Exchange solutions.
            </p>
          </Col>
        </Row>
        <Row className="rg_50 mb_50">
          <Col
            xl={4}
            md={6}
            className=" justify-content-center d-flex d-sm-flex d-md-block align-items-center"
          >
            <div className="centralize_feature_card">
              <div className="centralize_feature_title">Secure login</div>
              <div className="centralize_feature_content">
                Implement advanced encryption protocols and multi-factor
                authentication to fortify user access security on the
                centralized exchange platform.
              </div>
            </div>
          </Col>
          <Col
            xl={4}
            md={6}
            className=" justify-content-center d-flex d-sm-flex d-md-block align-items-center"
          >
            <div className="centralize_feature_card">
              <div className="centralize_feature_title">
                Extensive app functionality
              </div>
              <div className="centralize_feature_content">
                Provide users with a feature-rich application, offering diverse
                trading options, real-time market data, and portfolio management
                for a comprehensive trading experience.
              </div>
            </div>
          </Col>
          <Col
            xl={4}
            md={6}
            className=" justify-content-center d-flex d-sm-flex d-md-block align-items-center"
          >
            <div className="centralize_feature_card">
              <div className="centralize_feature_title">
                Custom Data-Driven Interface (DDI)
              </div>
              <div className="centralize_feature_content">
                Tailor the interface to user preferences, leveraging data
                analytics to offer personalized insights, charts, and trading
                tools for an intuitive and efficient experience.
              </div>
            </div>
          </Col>
          <Col
            xl={4}
            md={6}
            className=" justify-content-center d-flex d-sm-flex d-md-block align-items-center"
          >
            <div className="centralize_feature_card">
              <div className="centralize_feature_title">High Liquidity</div>
              <div className="centralize_feature_content">
                Ensure a robust market environment with high liquidity, enabling
                swift and efficient buy-sell transactions with minimal slippage
                on the centralized exchange.
              </div>
            </div>
          </Col>
          <Col
            xl={4}
            md={6}
            className=" justify-content-center d-flex d-sm-flex d-md-block align-items-center"
          >
            <div className="centralize_feature_card">
              <div className="centralize_feature_title">ICO Support</div>
              <div className="centralize_feature_content">
                Facilitate token launches seamlessly by offering comprehensive
                support for Initial Coin Offerings (ICOs), from token creation
                to trading integration on the platform.
              </div>
            </div>
          </Col>
          <Col
            xl={4}
            md={6}
            className=" justify-content-center d-flex d-sm-flex d-md-block align-items-center"
          >
            <div className="centralize_feature_card">
              <div className="centralize_feature_title">
                Order Matching Engine
              </div>
              <div className="centralize_feature_content">
                Utilize a powerful and scalable order matching engine that
                executes trades swiftly, ensuring timely and accurate order
                fulfillment on the centralized exchange.
              </div>
            </div>
          </Col>
          <Col
            md={6}
            xl={{ span: 4, offset: 2 }}
            className=" justify-content-center d-flex d-sm-flex d-md-block align-items-center"
          >
            <div className="centralize_feature_card">
              <div className="centralize_feature_title">
                Automate Trading Bots
              </div>
              <div className="centralize_feature_content">
                Enable users to optimize trading strategies by integrating and
                deploying automated trading bots, enhancing efficiency and
                responsiveness to market conditions.
              </div>
            </div>
          </Col>
          <Col
            xl={4}
            md={6}
            className=" justify-content-center d-flex d-sm-flex d-md-block align-items-center"
          >
            <div className="centralize_feature_card">
              <div className="centralize_feature_title">Escrow Application</div>
              <div className="centralize_feature_content">
                {" "}
                Implement a secure escrow application to facilitate and
                safeguard transactions, fostering trust and compliance within
                the centralized exchange ecosystem.
              </div>
            </div>
          </Col>
          <Col md={12}>
            <ConnectButton justifyContentPosition="connect_center" />
          </Col>
        </Row>
        <Row className="mt_50 pt_30 rg_30">
          <Col md={10} className="mx-auto text-center">
            <h2>Popular centralized crypto exchange we offer</h2>
          </Col>
          <Col md={8} className="mx-auto text-center">
            <p>
              There are many sought-after clone scripts in the market for
              centralized crypto exchanges. Let's explore some of the more
              popular options.
            </p>
          </Col>
        </Row>
        <Row className="mt_50 rg_30">
          <Col xl={2} md={4} sm={6} className="col_flex">
            <div className="centralize_crypto">
              <StaticImage
                className="centralize_crypto_logo"
                src="../../styles/assets/images/centralize-exchange/binance.webp"
                formats={["auto", "webp"]}
              />

              <span className="centralize_crypto_name">Binance</span>
            </div>
          </Col>
          <Col xl={2} md={4} sm={6} className="col_flex">
            <div className="centralize_crypto">
              <StaticImage
                className="centralize_crypto_logo"
                src="../../styles/assets/images/centralize-exchange/coinbase.webp"
                formats={["auto", "webp"]}
              />

              <span className="centralize_crypto_name">Coinbase</span>
            </div>
          </Col>
          <Col xl={2} md={4} sm={6} className="col_flex">
            <div className="centralize_crypto">
              <StaticImage
                className="centralize_crypto_logo"
                src="../../styles/assets/images/centralize-exchange/okx.webp"
                formats={["auto", "webp"]}
              />

              <span className="centralize_crypto_name">OKX</span>
            </div>
          </Col>
          <Col xl={2} md={4} sm={6} className="col_flex">
            <div className="centralize_crypto">
              <StaticImage
                className="centralize_crypto_logo"
                src="../../styles/assets/images/centralize-exchange/kucoin.webp"
                formats={["auto", "webp"]}
              />

              <span className="centralize_crypto_name">Kucoin</span>
            </div>
          </Col>
          <Col xl={2} md={4} sm={6} className="col_flex">
            <div className="centralize_crypto">
              <div className="centralize_crypto_logo">
                <StaticImage
                  src="../../styles/assets/images/centralize-exchange/huobi-logo.webp"
                  formats={["auto", "webp"]}
                />
              </div>
              <span className="centralize_crypto_name">Huobi</span>
            </div>
          </Col>

          <Col xl={2} md={4} sm={6} className="col_flex">
            <div className="centralize_crypto">
              <StaticImage
                className="centralize_crypto_logo"
                src="../../styles/assets/images/centralize-exchange/bitfinex.webp"
                formats={["auto", "webp"]}
              />

              <span className="centralize_crypto_name">Bitfinex</span>
            </div>
          </Col>
        </Row>
        <Row className="mt_50 pt_50 rg_30">
          <Col xl={10} className="mx-auto text-center">
            <h2>
              Business Benefits of our Centralized Exchange Development Services
            </h2>
          </Col>
          <Col xl={6} className="mx-auto text-center">
            <p>
              Explore the business benefits of our centralized crypto exchange
              development services.
            </p>
          </Col>
        </Row>
        <Row className="mt_30 rg_30">
          <Col
            lg={4}
            sm={6}
            className="d-flex justify-content-center align-items-center d-sm-block"
          >
            <div className="centralize_service_card">
              <span className="centralize_service_text">Customization</span>
            </div>
          </Col>
          <Col
            lg={4}
            sm={6}
            className="d-flex justify-content-center align-items-center d-sm-block"
          >
            <div className="centralize_service_card">
              <span className="centralize_service_text">Security Measures</span>
            </div>
          </Col>
          <Col
            lg={4}
            sm={6}
            className="d-flex justify-content-center align-items-center d-sm-block"
          >
            <div className="centralize_service_card">
              <span className="centralize_service_text">
                User-Friendly Interface
              </span>
            </div>
          </Col>
          <Col
            lg={4}
            sm={6}
            className="d-flex justify-content-center align-items-center d-sm-block"
          >
            <div className="centralize_service_card">
              <span className="centralize_service_text">
                Advanced Trading Features
              </span>
            </div>
          </Col>
          <Col
            lg={4}
            sm={6}
            className="d-flex justify-content-center align-items-center d-sm-block"
          >
            <div className="centralize_service_card">
              <span className="centralize_service_text">
                Compliance and Regulations
              </span>
            </div>
          </Col>
          <Col
            lg={4}
            sm={6}
            className="d-flex justify-content-center align-items-center d-sm-block"
          >
            <div className="centralize_service_card">
              <span className="centralize_service_text">Technical Support</span>
            </div>
          </Col>
          <Col
            lg={{ span: 4, offset: 2 }}
            sm={6}
            className="d-flex justify-content-center align-items-center d-sm-block"
          >
            <div className="centralize_service_card">
              <span className="centralize_service_text">
                Liquidity Enhancement
              </span>
            </div>
          </Col>
          <Col
            lg={4}
            sm={6}
            className="d-flex justify-content-center align-items-center d-sm-block"
          >
            <div className="centralize_service_card">
              <span className="centralize_service_text">
                Cost-Effective Solutions
              </span>
            </div>
          </Col>
        </Row>

        <CentralizedProcess />
        <Row className="mt_50 pt_30 rg_50">
          <Col md={10} className="mx-auto text-center">
            <h2>
              Why Should You Use Justtry Tech to Develop Centralized Crypto
              Exchanges?
            </h2>
          </Col>
          <Col lg={6}>
            <p>
              Selecting Justtry for centralized crypto exchange development is a
              prudent choice given their demonstrated expertise and seasoned
              professionals, ensuring the delivery of a secure and feature-rich
              platform. The company prioritizes customization, offering tailored
              solutions that accommodate specific client requirements and market
              trends. Justtry employs cutting-edge technology and industry best
              practices to develop a robust and high-performance exchange,
              emphasizing scalability and flexibility. With a commitment to
              timely project delivery, Justtry stands out for its efficient
              project management, assuring clients of a reliable and optimized
              solution for their crypto exchange needs.
            </p>
            <div className="crypto_btns_grp mt_30 justify-content-start">
              <Link className="prime_submit_btn" to="/contact-us">
                View Live Demo
              </Link>
              <button className="second_consult_btn" type="button">
                <TalkToUsNew label="Free Consult" db={db} />
              </button>
            </div>
          </Col>
          <Col
            lg={6}
            className="d-flex justify-content-center align-items-center d-lg-block"
          >
            <StaticImage
              src="../../styles/assets/images/centralize-exchange/centralize-choose.webp"
              formats={["auto", "webp"]}
            />
          </Col>
        </Row>
      </Container>
      <Faq faqData={CentralizedFaq} />
    </Fragment>
  );
};

export default CentralizedCryptoScreen;
