import React from "react";
import Layout from "../components/layout";
import Seo from "../components/seo";
import BreadCrumb from "../components/common/breadcrumb";
import { initializeApp } from "firebase/app";
import { getFirestore } from "firebase/firestore/lite";
import { graphql } from "gatsby";
import MoveForward from "../components/common/move-forward";
import CentralizedCryptoScreen from "../screens/centralized-crypto";

const CentralizedCryptoPage = (props) => {
  const app = initializeApp(props?.data?.site?.siteMetadata?.firestore);
  const db = getFirestore(app);
  const breadcrumbLinks = [
    { text: "Home", link: "/" },
    { text: "Centralized Crypto Exchange Development Company", link: null },
  ];
  return (
    <Layout db={db}>
      <BreadCrumb items={breadcrumbLinks} />
      <main>
        <CentralizedCryptoScreen db={db} />
      </main>
      <MoveForward db={db} />
    </Layout>
  );
};
export const Head = () => (
  <Seo
    title="Centralized Crypto Exchange Development Company | Justtry Technologies"
    headTitle="Centralized Crypto Exchange Development Company | Justtry Technologies"
    description="Justtry Tech is a leading centralized crypto exchange development firm, creating a centralized exchange platform like Binance with an efficient matching engine.that enables customers to buy and sell instantaneously by matching orders with the maximum precision."
    keywords="Centralized crypto exchange, Centralized exchange, Centralized Crypto, Centralized Crypto exchange development company, Centralized Crypto exchange development services, CEX trading platform, Crypto exchange development services"
  ></Seo>
);

export default CentralizedCryptoPage;

export const queryData = graphql`
  query MyQuery {
    site {
      siteMetadata {
        firestore {
          apiKey
          appId
          authDomain
          messagingSenderId
          projectId
          storageBucket
        }
      }
    }
  }
`;
